<template>
  <div id="app" :class="backgroundClass">
    <div class="main">
      <router-view />
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    backgroundClass () {
      const path = this.$route.path
      if (path.includes('mw3')) return 'mw3'
      if (path.includes('bo2')) return 'bo2'
      return 'mw3'
    }
  }
}
</script>
<style lang="stylus" scoped>
#app
  height 100vh
  width 100vw
  &::before
    content ""
    background-repeat no-repeat
    background-size cover
    background-position center bottom
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    opacity 0.25
    transition: background-image .40s ease-in-out;
    z-index -1
    background-image url('~assets/login_bg.png')
</style>
